import React, {Component} from 'react';

class WorkItem extends Component{
    render(){
        var {Icon, Title, Details, Class, delay, Image, link} = this.props;
                    
        const item = Image
        ? <img style={{width:60, marginTop: 24}} src={require("../../assets/image/" + Image)} /> 
        : <span className={Icon}></span>

        return(
            <div className={"col-md-4 work-item " + Class} data-wow-delay={delay}>
                <a href={link}>
                <div className="icon">
                    {item}
                </div>
                <h2>{Title}</h2>
                </a>
                <p>{Details}</p>
            </div>
        )
    }
}
export default WorkItem;